import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../../components/panel/panel.jsx";
import { connect } from "react-redux";
import { postDataNoTImeOut } from "../../../../config/axios.jsx";
import { reduxForm } from "redux-form";
import HeadLaporanTerimaBarang from "./HeadLaporanTerimaBarang.jsx";
import { formatDate, localStorageencryp } from "../../../../components/helpers/function.jsx";
import { ToastNotification } from "../../../../components/helpers/notification.jsx";
import * as moment from "moment-timezone";

// import ExportExel from "./ExportExel.jsx";
const maptostate = (state) => {
  return {
    hideModal: state.datamaster.modalDialog,
  };
};

class FormLaporanTerimaBarang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      dataterimabarang: [],
      export: false,
      
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });
    let data_post = {
      tgl_awal: formatDate(moment(new Date(data.tgl_awal)).format('YYYY-MM-DD')),
      tgl_akhir: formatDate(moment(new Date(data.tgl_akhir)).format('YYYY-MM-DD')),
    };
    postDataNoTImeOut("terimabarang/get/report", data_post)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
          this.setState({
            export: false,
            isLoading: false,
          });
        } else {
          ToastNotification("success", "Laporan Terima Barang Tersedia");
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          this.setState({
            export: true,
            isLoading: false,
            dataterimabarang: res.data,
          });
        }
      })
      .catch((err) => {
        this.setState({
          export: false,
          isLoading: false,
        });
        ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
      });
  }
  
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan Barang</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Terima Barang</li>
        </ol>
        <h1 className="page-header">Laporan Terima Barang </h1>
        <Panel>
          <PanelHeader>Filter Laporan</PanelHeader>
          <br />
          <div className="container">
            <HeadLaporanTerimaBarang
              export={this.state.export}
              isLoading={this.state.isLoading}
              dataterimabarang={this.state.dataterimabarang}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </div>
          <br />
        </Panel>
      </div>
    );
  }
}

FormLaporanTerimaBarang = reduxForm({
  form: "LaporanTerimaBarang",
  enableReinitialize: true,
})(FormLaporanTerimaBarang);
export default connect(maptostate, null)(FormLaporanTerimaBarang);
