import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { getUserdata, localStoragedecryp } from "../../../../../components/helpers/function";

class LaporanTerimaBarangExel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    // const total = this.props.data.map((row) =>
    // row.detail.map((key) => key.stock_on_hand).reduce((a, b) => a + b, 0)
    // )

    let stock_on_hand = 0;
    let berat = 0;
    let berat_atribut = 0;
    let total_barang = 0;
    this.props.data.forEach((barang, index) => {
      barang.detail.forEach((item, index) => {
        stock_on_hand = stock_on_hand + item.stock_on_hand;
        berat_atribut = berat_atribut + item.berat_atribut;
        berat = berat + item.berat;
      });
      total_barang = total_barang + barang.detail.length;
    });
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Terima Barang"
          sheet="Laporan Terima Barang"
          buttonText="Export Exel"
        />
        <table
          rowkey="kode_member"
          id="table-to-xls"
          style={{ display: "none" }}
        >
          <thead>
            <tr>
              <th colSpan="10"> Periode </th>
            </tr>
            <tr>
              <th colSpan="10">
                {" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
                    "s/d" +
                    JSON.parse(localStoragedecryp("tgl_laporan"))
                      .tgl_akhir}{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="10"> TOKO :  {getUserdata().nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="10"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
            <tr>
              <th colSpan="10"></th>
            </tr>
            <tr>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                NO
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                TANGGAL TERIMA
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                KODE BARANG
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                KODE BARCODE
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                KODE INTERN
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                NAMA BARANG
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                KODE GUDANG
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                KODE TOKO
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                BERAT
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                NAMA ATRIBUT
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                BERAT ATRIBUT
              </th>
            </tr>
          </thead>
          <tbody>
            {this.props.data.map((item, no) => (
              <>
                {/* <>
                  <tr>
                    <th style={{ textAlign: "left" }}> No Terima </th>
                    <th colSpan="9" style={{ textAlign: "left" }}>
                      {" "}
                      {item._id.no_terima}{" "}
                    </th>
                  </tr>
                </> */}
                {item.detail.map((row, index) => (
                  <>
                    <tr>
                      <td style={{textAlign : "center"}}> {++no}</td>
                      <td style={{textAlign : "center"}}> {row.tgl_terima}</td>
                      <td> {row.kode_barang}</td>
                      <td style={{textAlign : "center"}}> {row.kode_barcode}</td>
                      <td> {row.kode_intern}</td>
                      <td> {row.nama_barang}</td>
                      <td style={{textAlign : "center"}}> {row.kode_gudang}</td>
                      <td style={{textAlign : "center"}}> {row.kode_toko}</td>
                      <td> {row.berat}</td>
                      <td> {row.nama_atribut}</td>
                      <td> {row.berat_atribut}</td>
                    </tr>
                  </>
                ))}
              </>
            ))}
          </tbody>
          <tfoot>
            <tr>
              <th
                colSpan="8"
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "center",
                }}
              >
                {" "}
                Total{" "}
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}>
                {berat.toFixed(3)}
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}> </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right"  }}>
                {berat_atribut}
              </th>
            </tr>
            {/* <tr>
              <th
                colSpan="10"
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "left",
                }}
              >
                Total Brang : {total_barang} Pcs
                <br />
                Total Berat : {berat.toFixed(3)} <br />
                Total Berat Atribut : {berat_atribut}
              </th>
            </tr> */}
          </tfoot>
        </table>
      </>
    );
  }
}
export default LaporanTerimaBarangExel;
