import React, { Component } from "react";

import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { InputDate } from "../../../../components/helpers/field";
import { getTglSystem } from "../../../../components/helpers/function";
import Tabel from "../../../../components/helpers/tabel";
// import Tabel from "../../../../components/helpers/tabel";
import ValidasiTanggalLaporan from "../../../../Validasi/ValidasiTanggalLaporan";
import LaporanTerimaBarangExel from "./exel/LaporanTerimaBarangExel";
// import LaporanBatalServiceExel from "./exel/LaporanBatalServiceExel";
import LaporanTerimaBarangPdf from "./pdf/LaporanTerimaBarangPdf";
import Loading from 'react-fullscreen-loading';

class HeadLaporanTerimaBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  handleSubmit(data) {
    console.log(data);
  }
  exportPdf() {
    LaporanTerimaBarangPdf(this.props.dataterimabarang);
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }

  render() {
    let databarang = [];
    let berat = 0;
    this.props.dataterimabarang.forEach((element) => {
      element.detail.forEach((detail) => {
        databarang.push(detail);
        berat = berat + detail.berat
      });
    });
    const columns = [
  
      {
        dataField: "tgl_terima",
        text: "Tanggal Terima",
        footerAttrs: {
          colSpan: "4",
        },
        footer: (cell, row, num, index) => {
          return <div>Total Barang : {databarang.length} </div>;
        },
      },
      {
        dataField: "kode_barang",
        text: "Kode Barang",
      },
      {
        dataField: "kode_barcode",
        text: "Kode Barcode",
      },

      {
        dataField: "nama_barang",
        text: "Nama Barang",
      },
     
      {
        dataField: "berat",
        text: "Berat",
        footer: (cell, row, num, index) => {
          return <div>{berat.toFixed(3)} </div>;
        },
        formatter: (cell) => {
          return cell.toFixed(3);
        },
      }
    ];
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-lg-4">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
              // customInput={<CustomeInputDate />}
            />
          </div>

          <div className="col-lg-4">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
            />
          </div>

          <div className={this.props.export ? "col-lg-4  d-none" : "col-lg-4"}>
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
          <div className={this.props.export ? "col-lg-4" : "col-lg-4 d-none"}>
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
          <div className="col-12">
            <Tabel
              keyField="kode_gudang"
              data={this.props.export ? databarang  :  []}
              columns={columns}
              empty={true}
              textEmpty="Data Laporan Terima Barang Kosong"
            />
          </div>
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            {/* <label htmlFor="">&nbsp;</label> */}
            <div className="row">
              <div className="col-lg-6">
                <button
                  onClick={() => this.exportPdf()}
                  className="btn btn-warning btn-block"
                >
                  {" "}
                  Export PDF{" "}
                </button>
              </div>
              <div className="col-lg-6">
                <LaporanTerimaBarangExel data={this.props.dataterimabarang} />
              </div>
            </div>
          </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

HeadLaporanTerimaBarang = reduxForm({
  form: "HeadLaporanTerimaBarang",
  enableReinitialize: true,
  validate: ValidasiTanggalLaporan,
})(HeadLaporanTerimaBarang);
// export default connect()(HeadLaporanTerimaBarang);
export default connect((state) => {
  return {
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadLaporanTerimaBarang);
