import jsPDF from "jspdf";
import "jspdf-autotable";
import {
  getToday,
  getUserdata,
  localStoragedecryp,
} from "../../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanTerimaBarangPdf = (
  data = "",
  username = "",
  tanggal_dari = "",
  tanggal_sampai = ""
) => {
  // initialize jsPDF
  const doc = new jsPDF("l", "mm", [297, 210]);
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let footRows = [];
  let finalY = 30;

  let grandtotalqty = 0;
  let grantotalberat = 0;
  let grantotalberatatr = 0;
  let total_barang = 0;

  doc.setFontSize(15);
  doc.text("LAPORAN TERIMA BARANG", 14, 15);
  doc.setFontSize(20);
  doc.text(getUserdata().nama_toko, 200, 15);
  doc.setFontSize(8);
  let jml_alamat = getUserdata().alamat_toko.length;
  if (jml_alamat > 20) {
    doc.text(getUserdata().alamat_toko.slice(0,48), 200, 20);
  }
  if (jml_alamat > 50) {
    doc.text(getUserdata().alamat_toko.slice(48,90), 200, 25);
  }
  doc.setFontSize(10);
  doc.setProperties({
    title: "Laporan Terima Barang",
  });
  doc.text(
    `PERIODE : ${
      localStoragedecryp("tgl_laporan").length === 0
        ? null
        : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
          " s/d " +
          JSON.parse(localStoragedecryp("tgl_laporan")).tgl_akhir
    }`,
    14,
    25
  );
  let tableColumn = [
    // [
    //   {
    //     content: `NO TERIMA : ${item._id.no_terima}`,
    //     colSpan: 9,
    //   },
    // ],
    [
      {
        content: `NO`,
      },
      {
        content: `TANGGAL TERIMA`,
      },
      {
        content: `KODE BARANG`,
      },
      {
        content: `KODE BARCODE`,
      },
      {
        content: `KODE INTERN`,
      },
      {
        content: `NAMA BARANG`,
      },
      {
        content: `KODE GUDANG`,
      },
      {
        content: `KODE BAKI`,
      },
      {
        content: `BERAT`,
      },
      {
        content: `NAMA ATRIBUTE`,
      },
      {
        content: `BERAT ATRIBUTE`,
      },
      // {
      //   content: `STOCK ON HAND`,
      // },
    ],
  ];
  let i = 1;
  data.forEach((item, no) => {
    let sub_qty = 0;
    let berat_atribut = 0;
    let sub_berat = 0;

    // let data = [
    //   {
    //     content: `NO TERIMA : ${item._id.no_terima}`,
    //     colSpan: "9",
    //   },
    // ];
    // tableRows.push(data);
    item.detail.forEach((barang, index) => {
      let rows = [
        i,
        {
          content: barang.tgl_terima,
          style : {
            halign : 'center'
          }
        },
        barang.kode_barang,
        barang.kode_barcode,
        barang.kode_intern,
        barang.nama_barang,
        barang.kode_gudang,
        barang.kode_toko,
        barang.berat.toFixed(3),
        barang.nama_atribut,
        barang.berat_atribut.toFixed(3),
        // barang.stock_on_hand,
      ];
      tableRows.push(rows);

      sub_qty = sub_qty + parseFloat(barang.stock_on_hand);
      berat_atribut = berat_atribut + parseFloat(barang.berat_atribut);
      sub_berat = sub_berat + parseFloat(barang.berat);

      grandtotalqty = grandtotalqty + parseFloat(barang.stock_on_hand);
      grantotalberat = grantotalberat + parseFloat(barang.berat);
      grantotalberatatr = grantotalberatatr + parseFloat(barang.berat_atribut);
      ++i
    });
    total_barang = total_barang + item.detail.length;

    // let total = [
    //   {
    //     content: `Total`,
    //     colSpan: 7,
    //     styles: {
    //       halign: 'center',
    //       fillColor: "#E8E5E5",
    //       textColor: "#000",
    //     },
    //   },
    //   {
    //     content: `${sub_berat.toFixed(3)}`,
    //     styles: {
    //       fillColor: "#E8E5E5",
    //       textColor: "#000",
    //     },
    //   },
    //   {
    //     content: ``,
    //     styles: {
    //       fillColor: "#E8E5E5",
    //       textColor: "#000",
    //     },
    //   },
    //   {
    //     content: `${berat_atribut.toFixed(3)}`,
    //     styles: {
    //       fillColor: "#E8E5E5",
    //       textColor: "#000",
    //     },
    //   },
    //   // {
    //   //   content: `Total`,
    //   //   colSpan: 7,
    //   // },
    //   // `${sub_berat.toFixed(3)}`,
    //   // "",
    //   // `${berat_atribut.toFixed(3)}`,
    //   // `${sub_qty}`,
    // ];
    // tableRows.push(total);
  });

  let footer = [
    {
      content: `Total `,
      colSpan: 8,
      styles: {
        halign: 'center',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${grantotalberat.toFixed(3)}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: ``,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${grantotalberatatr.toFixed(3)}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    // {
    //   content: `Total Barang : ${total_barang} Pcs\nTotal Berat : ${grantotalberat.toFixed(
    //     3
    //   )}\nTotal Berat Atribut : ${grantotalberatatr.toFixed(3)}`,
    //   colSpan: 10,
    //   styles: {
    //     fillColor: "#E8E5E5",
    //     textColor: "#000",
    //   },
    // },
  ];

  tableRows.push(footer);
  let printed = [
    {
      colSpan:9,
      content: `Printed By ${JSON.parse(localStoragedecryp("userdata")).user_id} / ${getToday()}`,
      styles: {
        // lineWidth: 0.1,
        fontStyle: "italic",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ]
  tableRows.push(printed);
  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    foot: footRows,
    startY: finalY,
    theme: "plain",
    rowPageBreak: "avoid",
    // pageBreak: "avoid",
    margin: { top: 20 },
    // bodyStyles: { lineWidth: 0.02, lineColor: "#000" },
    // footStyles: { lineWidth: 0.02, lineColor: "#000" },
    headStyles: {
      // lineWidth: 0.02,
      // lineColor: "#000",
      fillColor: "#E8E5E5",
      textColor: "#000",
    },
  });
  finalY = doc.autoTableEndPosY() + 10;
  // tableRows = [];
  // footRows = [];
  // sub_qty = 0;
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left

  // doc.text(
  //   `User \t\t\t:  ${JSON.parse(localStoragedecryp("userdata")).user_id}`,
  //   14,
  //   finalY + 10
  // );
  // doc.text(`Cetak\t\t\t: ${getToday()}`, 14, finalY + 16);
  // doc.text(`Grand Total Qty\t: ${grandtotalqty}`, 14, finalY + 22);
  // doc.text(
  //   `Grand Total Berat     : ${grantotalberat.toFixed(3)}`,
  //   14,
  //   finalY + 28
  // );
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  // doc.autoPrint();
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>Laporan Terima Barang</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanTerimaBarangPdf;

